<template lang="html">
<div class="measuresSo">
  <h1></h1>
  <el-form label-width="100px" label-position="left">
    <section>
      <h2></h2>
      <p> {{projectNumber}}</p>
      <p> {{projectName}}</p>
    </section>
    <section>
      <h2></h2>
      <el-form-item label="">
        <el-select v-model="projectPhaseId" placeholder="">
         <el-option
           v-for="projectPhase in projectPhases"
           :key="projectPhase.id"
           :label="projectPhase.name"
           :value="projectPhase.id">
         </el-option>
       </el-select>
     </el-form-item>
      <el-form-item label="">
        <el-select v-model="soLocationNumber" placeholder="SO-01">
         <el-option
           v-for="location in project.soLocation"
           :key="location.number"
           :label="location.number"
           :value="location.number">
         </el-option>
       </el-select>
     </el-form-item>
      <el-form-item label="WISE IP">
        <el-input :value="wiseIP" >
        </el-input>
      </el-form-item>
      <el-form-item label=" 1m " label-width="300px">
      </el-form-item>
    <el-button @click="measures" :disabled="measuresSoDatas.length === 10"></el-button>
    </section>
    <section v-if="measuresSoDatas.length">
      <p>10{{measuresSoDatas.length}}</p>
      <el-table
        :data="measuresSoDatas"
        style="width: 100%">
        <el-table-column
          fixed
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="time"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="temp"
          label="(c)"
          width="120">
        </el-table-column>
        <el-table-column
          prop="VoltageX"
          label=""
          width="120">
        </el-table-column>
        <el-table-column
          prop="degreeX"
          label="(C)"
          width="120">
        </el-table-column>
        <el-table-column
          prop="displacement"
          label="(cm)"
          width="120">
        </el-table-column>
        <el-table-column
          prop="totalDisplacement"
          label="(cm)"
          width="120">
        </el-table-column>
        <el-table-column
          prop="depth"
          label="(m)"
          width="120">
        </el-table-column>
      </el-table>
      <el-button @click="clearMeasuresDatas"></el-button>
      <el-button @click="uploadMeasuresDatas" :disabled="!measuresSoDatas.length" ></el-button>
    </section>
  </el-form>
  <p></p>
</div>
</template>

<script>
import startMeasures from '@/utils/measuresSo'
export default {
  data() {
    return {
      wiseIP: '192.168.58.200',
      measuresSoDatas: [],
      projectId: '',
      projectPhaseId: '',
      soLocationNumber: ''
    }
  },
  methods: {
    measures: function() {
      startMeasures(this.wiseIP, this.measuresSoDatas)
    },
    clearMeasuresDatas: function() {
      this.measuresSoDatas = []
    },
    uploadMeasuresDatas: function() {
      let measuresData = {}
      measuresData.projectId = this.projectId
      measuresData.projectPhaseId = this.projectPhaseId
      measuresData.soLocationNumber = this.soLocationNumber
      measuresData.measureResult = this.measuresSoDatas
      this.$store.dispatch('uploadMeasuresDatas', JSON.stringify(measuresData))
    },
    getProjectId: function() {
      this.projectId = this.$route.params.projectId
    },
    getProjectPhases: function() {
      this.$store.dispatch('getProjectPhases')
    }
  },
  computed: {
    projects: function() {
      return this.$store.getters.projects
    },
    project: function() {
      return this.$store.getters.currentProject
    },
    projectNumber: function() {
      return this.project.number
    },
    projectName: function() {
      return this.project.name
    },
    projectPhases: function() {
      return this.$store.getters.projectPhases
    }
  },
  mounted() {
    this.getProjectId(), this.getProjectPhases(), this.getProject()
  }
}
</script>
